import { getCurrentUserData } from '../services/service';


const defaultState = {
    info: null,
    isAuth: false
}

export const userReducer = (state: any = defaultState, action: any) => {
    console.log(state)
    console.log(action)

    switch (action.type) {
        case "UPDATE_USER_DATA":
            return {...state, isAuth: action.payload}
        case "GET_USER_DATA":
            getCurrentUserData()
                .then((result) => {
                    return result
                })
        default:
            return state
    }
}
